body {
  background-color: #1f2028;
  text-align: center;
}

.logo {
  display: flex;
  justify-content: center;
  margin-top: 8.6rem;
}

#calc {
  height: 14.1rem;
}

/* Andreas: Trying to get gradient to work on the hover from top to bottom. Krisitan made one that works from side to side but can't get the transition from top to bottom */
#submit {
  background: rgb(202, 67, 214);
  background: linear-gradient(
    90deg,
    rgba(202, 67, 214, 1) 2%,
    rgba(166, 79, 217, 1) 20%,
    rgba(101, 106, 222, 1) 45%,
    rgba(101, 106, 222, 1) 100%
  );
  background-size: 200%;
  background-position: right;
  color: white;
  border: none;
  height: 5rem;
  width: 22rem;
  border-radius: 5px;
  font-size: 2.4rem;
  transition: 0.4s ease-in-out;
  cursor: pointer;
  margin-top: 3rem;
}

#submit:hover {
  background-position: left;
}

.title {
  color: white;
  font-size: 4rem;
  margin-top: 8rem;
}

.desc {
  color: white;
  font-size: 2rem;
  margin: 6rem 3rem;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

textarea:focus,
input:focus {
  outline: none;
}

::selection {
  color: rgba(101, 106, 222, 1);
  /* color: rgba(202, 67, 214, 1); */
}

#submit::selection {
  color: rgb(202, 67, 214);
  text-shadow: 0.1rem 0.1rem #1f2028;
}

#inputForm {
  margin-top: 6rem;
}

label {
  color: white;
  font-size: 2rem;
}

input {
  width: 6rem;
  height: 2.5rem;
  margin: 1rem;
}

#group {
  margin-left: 6.3rem;
}

#results {
  color: white;
  font-size: 2rem;
  margin: 3rem;
}

@media screen and (max-width: 440px) {
  #inputForm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #group {
    margin-left: 1rem;
  }

  #submit {
    width: 18rem;
  }
}
